.tooltip_container {
    position: relative;
    display: inline-block;
}

.tooltip_text {
    visibility: hidden;

    // width: 120px;
    height: max-content;
    width: max-content;
    background-color: #333;
    color: #fff;
    text-align: center;
    // border-radius: 6px;
    padding: 0.5em;
    position: fixed;
    z-index: 1;
    // bottom: 125%;
    // left: 50%;
    transform: translate(-50%, -150%);
    opacity: 0;
    transition: opacity 0.3s;

    /* Added visibility transition */
}

.tooltip_container:hover+.tooltip_text {
    visibility: visible;
    opacity: 1;
}