@use "./../../styles/Shared" as shared;

main.TSP {
    article {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    aside {
        width: 100%;

        display: grid;
        grid-template-columns: 80% 20%;
        margin: 0.1em;
        align-items: center;

        .stored {
            display: flex;
            overflow: scroll;

            div {
                margin: 0.1em
            }

            p {
                white-space: nowrap;
                margin: 0.1em;
            }
        }

        .sidebar {
            display: flex;
            flex-direction: column;
            justify-content: center;

            label {
                margin: 0.1em;
                display: flex;
                align-items: center;
            }

            button,
            input {
                cursor: pointer;
                margin: 0.1em;
                border-radius: 1em;
                background-color: shared.$accent1-color;
                color: shared.$primary-color;
                border: 0.1em solid shared.$accent1-color;
                width: 100%;
                text-align: center;

                &:hover,
                &:focus-visible {
                    background-color: shared.$primary-color;
                    color: shared.$accent1-color;
                    border: 0.1em solid shared.$accent1-color;
                    outline: none;
                }
            }
        }
    }

    svg {
        max-width: 100vh;
        max-height: 70vh;
        border: 1px solid shared.$accent1-color;

        path.user {
            stroke: shared.$accent1-color;

            &+text {
                transition: opacity 0.3s;
                fill: shared.$accent1-color;
            }
        }

        path {
            stroke: shared.$secondary-color;
            stroke-linejoin: round;

            &+text {
                visibility: hidden;

                opacity: 0;
                transition: opacity 0.3s;
                fill: shared.$secondary-color;
                /* Added visibility transition */
            }

            &:hover+text {
                visibility: visible;
                opacity: 1;
                z-index: 1;
            }

        }

        circle {
            cursor: pointer;

            &.active {

                fill: shared.$accent1-color
            }

            &.inactive {
                fill: shared.$secondary-dark-color
            }
        }

        text {
            user-select: none;
            pointer-events: none;
        }


    }
}